import { Component, OnInit } from "@angular/core";
import { SweetalertComponent } from "app/shared/sweetalert/sweetalert.component";
import { WholeSaleService } from "../../service/wholeSale.service";
import { Router } from "@angular/router";
import { ReportService } from "app/modules/reports/services/report.service";
import { customerInvoiceFabricReport } from "app/shared/reports/customerInvoiceFabric.service";

@Component({
  selector: "app-whole-sale",
  templateUrl: "./list-whole-sale.component.html",
  styleUrls: ["./list-whole-sale.component.scss"],
})
export class ListWholeSaleComponent implements OnInit {
  public contentHeader: Object;
  public tableDataArray = [];

  constructor(
    private _sw: SweetalertComponent,
    private _serviceData: WholeSaleService,
    private _router: Router,
    private pdfService: customerInvoiceFabricReport,
    private reportService: ReportService
  ) {}

  ngOnInit(): void {
    this.getAllDetail();

    this.contentHeader = {
      headerTitle: "Whole Sale ",
      actionButton: true,
      breadcrumb: {
        type: "",
        links: [
          {
            name: "Whole Sale Fabric",
            isLink: true,
            link: "/",
          },
          {
            name: "Detail",
            isLink: false,
          },
        ],
      },
    };
  }

  getAllDetail() {
    this._serviceData.getAll().subscribe((res) => {
      if (res.data) {
        this.tableDataArray = res.data;
      } else if (!res.data && this.tableDataArray.length == 1) {
        this.tableDataArray = [];
      }
    });
  }

  confirmDelete(id) {
    let deleteId = {
      id: id,
    };
    this._sw.delete(() =>
      this._serviceData.delete(deleteId).subscribe(
        (res) => {
          setTimeout(() => this.getAllDetail(), 2000);
        },
        (err) => {
          this._sw.fire("error", "GRN Records", "There is a problem");
        }
      )
    );
  }

  searchTerm: string = "";
  get filterTableData() {
    if (!this.searchTerm) {
      return this.tableDataArray;
    }

    const searchTermLowerCase = this.searchTerm.toLowerCase();

    return this.tableDataArray.filter(
      (item) =>
        item.supplierName.toLowerCase().includes(searchTermLowerCase) ||
        item.supplierNumber.toLowerCase().includes(searchTermLowerCase) ||
        item.transactionDate.toLowerCase().includes(searchTermLowerCase)
    );
  }

  makeReport(itemId) {
    this.reportService
      .customerInvoiceFabricReport({ invoiceId: itemId })
      .subscribe((getDetail) => {
        if (getDetail.data) {
          this.pdfService.generatePdfContent(getDetail.data);
          this._router.navigate(["/reports/view"]);
        }
      });
  }
}
